lr-single-selector label {
  float: left;
}
lr-single-selector div.input-control {
  float: right;
  display: inline-block;
}
lr-single-selector div.input-control span:nth-child(2) {
  display: inline-block;
  width: 3em;
  text-align: center;
}
lr-single-selector div.input-control span.glyphicon {
  cursor: pointer;
}